<template>
  <div id="app" class="wrapper">

    <co-header></co-header>
    <co-send-info></co-send-info>
    <co-map></co-map>
    <co-dialog></co-dialog>

    <swiper-present></swiper-present>

    <mt-swipe :auto="14000">
      <mt-swipe-item v-for="item in swiperBgPics"><img :src="item"></mt-swipe-item>
    </mt-swipe>

    <co-evaluate-btn></co-evaluate-btn>

    <mt-popup  v-model="popupVisible"  position="bottom">
      <co-evaluate></co-evaluate>
    </mt-popup>

    <!--<router-view></router-view>-->
  </div>
</template>

<script>
  import CoHeader from './components/header.vue'
  import CoSendInfo from './components/send-info.vue'
  import CoMap from './components/map.vue'
  import CoDialog from './components/my-dialog.vue'
  import CoEvaluate from './components/evaluate.vue'
  import CoEvaluateBtn from './components/evaluate-btn.vue'
  import SwiperPresent from './components/swiper-present.vue'

  import bus from './assets/eventBus'; // 非父子组件通信-中央事件总线

  export default {
    name: 'app',
    components: {
      CoHeader,CoSendInfo,CoMap,CoDialog,CoEvaluate,CoEvaluateBtn,SwiperPresent
    },
    created () {
      bus.$on('closeEvaPop', () => {
        this.closePop();
      });
      bus.$on('showEvaPop', () => {
          this.showPop();
      })
    },
    data () {
        return {
          popupVisible: false,
          swiperBgPics: [
              './static/imgs/focus.jpg', //注意静态图片需要放在static里边才能加载出来
              './static/imgs/focus2.jpg',
              './static/imgs/focus3.jpg'
          ]
        }
    },
    methods: {
        showPop () {
            this.popupVisible = true;
            bus.$emit('initData');
        },
        closePop () {
            this.popupVisible = false;
        }
    }
  }
</script>

<style lang="scss">
  .wrapper {
    margin: 0 auto;
    font-size: 0.16rem;
    color: #161418;
    margin-bottom: 0.96rem;

    .mint-popup {
      width: 100%;
    }

    .mint-swipe {
      height: 3.23rem;
      .mint-swipe-item {
        img {
          width: 100%;
        }
      }
      .mint-swipe-indicators {
        // 焦点原标
        .mint-swipe-indicator {
          background: #fff;
          opacity: 0.95;
        }
        // 焦点原标选中时
        .is-active {
          width: 20px;
          border-radius: 5px;
        }
      }

    }
  }
</style>



