<template>
    <div class="eva-wrapper">
      <div class="title">
        <span class="t-cont">评价配送员</span>
        <i id="eva-close" @click="closePop"></i>
      </div>
      <div class="eva-stars">
        <ul id="e-stars">
          <li data-index="0" v-for="(item, index) in starColor"
              class="star" :class="{dark: !item, light:item}"
              @click="starEva(index)"
          >
          </li>
        </ul>
        <ul id="star-comment">
          <li class="item" >{{starWord}}</li>
        </ul>
        <div class="s-c-info">您的评价对配送小哥很重要</div>
      </div>
      <div class="comment-wrap">
        <ul class="comment-ul" id="comment-ul">
          <template v-if="starNum === 1">
            <li class="li-item">
              <ul class="inner-ul" id="star-comment0">
                <li class="item"
                    v-for="(item, index) in lists.star1.comments"
                    @click="selectItem(index)" :class="{'cur-select':item.isSelected}">{{item.word}}
                </li>
              </ul>
            </li>
          </template>

          <template v-else-if="starNum === 2">  <!--todo注意else和else if的区别-->
            <li class="li-item">
              <ul class="inner-ul" id="star-comment1">
                <li class="item" v-for="(item, index) in lists.star2.comments"
                    @click="selectItem(index)" :class="{'cur-select':item.isSelected}">{{item.word}}
                </li>
              </ul>
            </li>
          </template>

          <template v-else-if="starNum === 3">
            <li class="li-item">
              <ul class="inner-ul" id="star-comment2">
                <li class="item" v-for="(item, index) in lists.star3.comments"
                    @click="selectItem(index)" :class="{'cur-select':item.isSelected}">{{item.word}}
                </li>
              </ul>
            </li>
          </template>

          <template v-else-if="starNum === 4">
            <li class="li-item">
              <ul class="inner-ul" id="star-comment3">
                <li class="item" v-for="(item, index) in lists.star4.comments"
                    @click="selectItem(index)" :class="{'cur-select':item.isSelected}">{{item.word}}
                </li>
              </ul>
            </li>
          </template>

          <template v-else-if="starNum === 5">
            <li class="li-item">
              <ul class="inner-ul" id="star-comment4">
                <li class="item" v-for="(item, index) in lists.star5.comments"
                    @click="selectItem(index)" :class="{'cur-select':item.isSelected}">{{item.word}}
                </li>
              </ul>
            </li>
          </template>

        </ul>
      </div>
      <div class="your-comments">
        <textarea id="y-c-content" placeholder="其他想说的"></textarea>
      </div>
      <a id="com-submit" class="com-submit" :class="{'sub-disabled': subDisabled}" @click="submit">匿名提交</a>
    </div>
</template>

<script>
  import bus from '../assets/eventBus'; // 非父子组件通信-中央事件总线
  export default {
      name: 'co-evaluate',
      props: ['init'],
      data () {
          return {
              starNum: 0,
              starColor: [0, 0, 0, 0, 0],
              starWord: '',
              starTags: [],
              lists: null,
              subDisabled: true
          }
      },
      created () {
          bus.$on('initData', () => {
              this.starNum = 0;
              this.starColor = [0, 0, 0, 0, 0];
              this.starWord = '';
              this.starTags = [];
              this.lists = {
                star1: {
                  starCom: '好',
                  comments: [
                    {
                      isSelected: false,
                      word: '配送慢1'
                    },
                    {
                      isSelected: false,
                      word: '态度恶劣1'
                    },
                    {
                      isSelected: false,
                      word: '未送达指定地1'
                    },
                    {
                      isSelected: false,
                      word: '包装损害严重1'
                    },
                    {
                      isSelected: false,
                      word: '没耐心1'
                    },
                    {
                      isSelected: false,
                      word: '没耐不负责任1'
                    }
                  ]
                },
                star2: {
                  starCom: '很好',
                  comments: [
                    {
                      isSelected: false,
                      word: '配送慢2'
                    },
                    {
                      isSelected: false,
                      word: '态度恶劣2'
                    },
                    {
                      isSelected: false,
                      word: '未送达指定地2'
                    },
                    {
                      isSelected: false,
                      word: '包装损害严重2'
                    },
                    {
                      isSelected: false,
                      word: '没耐心2'
                    },
                    {
                      isSelected: false,
                      word: '没耐不负责任2'
                    }
                  ]
                },
                star3: {
                  starCom: '极好',
                  comments: [
                    {
                      isSelected: false,
                      word: '配送慢3'
                    },
                    {
                      isSelected: false,
                      word: '态度恶劣3'
                    },
                    {
                      isSelected: false,
                      word: '未送达指定地3'
                    },
                    {
                      isSelected: false,
                      word: '包装损害严重3'
                    },
                    {
                      isSelected: false,
                      word: '没耐心3'
                    },
                    {
                      isSelected: false,
                      word: '没耐不负责任3'
                    }
                  ]
                },
                star4: {
                  starCom: '非常好',
                  comments: [
                    {
                      isSelected: false,
                      word: '配送慢4'
                    },
                    {
                      isSelected: false,
                      word: '态度恶劣4'
                    },
                    {
                      isSelected: false,
                      word: '未送达指定地4'
                    },
                    {
                      isSelected: false,
                      word: '包装损害严重4'
                    },
                    {
                      isSelected: false,
                      word: '没耐心4'
                    },
                    {
                      isSelected: false,
                      word: '没耐不负责任4'
                    }
                  ]
                },
                star5: {
                  starCom: '好爆了！',
                  comments: [
                    {
                      isSelected: false,
                      word: '配送慢5'
                    },
                    {
                      isSelected: false,
                      word: '态度恶劣5'
                    },
                    {
                      isSelected: false,
                      word: '未送达指定地5'
                    },
                    {
                      isSelected: false,
                      word: '包装损害严重5'
                    },
                    {
                      isSelected: false,
                      word: '没耐心5'
                    },
                    {
                      isSelected: false,
                      word: '没耐不负责任5'
                    }
                  ]
                }
              };
              this.subDisabled = true;
          });

      },
      methods: {
        starEva (index) { // index需要在html引入

            this.starNum = index + 1; // 评星数目，展示不同平分内容区

            let starItem = this.getStarItem();
            this.starWord = starItem.starCom; // 星级解释语

            // 五星评价
            this.starColor = this.starColor.map((v, i) => {
                return i <= index ? 1 : 0;
            });

            // 标签选中初始化
            let starItemComments = starItem.comments;
            starItemComments.forEach((v, i) => {
                starItemComments[i].isSelected = false;
            });
            this.starTags = [];
            // 4星及以上激活按钮
            this.subDisabled = this.starNum <= 3;
        },
        selectItem (index) {
            console.log(index);
            let starComments =  this.getStarItem().comments;
            starComments[index].isSelected = !starComments[index].isSelected;

            let tags = [];
            starComments.forEach((v, i) => {
              if (v.isSelected) {
                tags.push(v.word);
              }
            });
            this.starTags = tags;


            // 4星以下选中标签时激活按钮
            this.subDisabled = this.starNum < 4 && this.starTags.length === 0;

        },
        closePop () {
            bus.$emit('closeEvaPop');
        },
        submit () {
            if (!this.subDisabled) {
              let starItem = this.getStarItem();
              let result = {
                starNum: this.starNum,
                starCom: starItem.starCom,
                starTag: this.starTags
              };
              console.log('提交的数据：',result);
              this.closePop();
            }
        },
        getStarItem () {
            return this.lists['star' + this.starNum];
        }

      }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/common.scss";
  .eva-wrapper {
    text-align: center;
    .title {
      padding: 0.26rem 0.48rem;
      border: 1px solid #EAEAEA;
      .t-cont {
        font-size: 0.3rem;
        color: #686868;
      }
      #eva-close {
        float: right;
        width: 0.27rem;
        height: 0.27rem;
        @include bgImg('../assets/imgs/close.png');
      }
    }
    .eva-stars {
      margin-top: 0.4rem;
      #e-stars {
        .star {
          width: 0.44rem;
          height: 0.44rem;
          margin-right: 0.05rem;
          display: inline-block;
        }
        .light {
          @include bgImg('../assets/imgs/light-star.png');
        }
        .dark {
          @include bgImg('../assets/imgs/dark-star.png');
        }
      }
      #star-comment {
        .item {
          margin-top: 0.25rem;
          font-size: 0.3rem;
          color: #f23030;
        }
      }
      .s-c-info {
        margin-top: 0.25rem;
        font-size: 0.3rem;
        color: #686868;
      }
    }
    .comment-wrap {
      padding: 0 0.3rem;
      .li-item {
        .item {
          float: left;
          width: 2.9rem;
          margin: 0.15rem 0.25rem;
          border: 1px solid #cbcbcb;
          border-radius: 30px;
          padding: 0.2rem 0;
          font-size: 0.32rem;
          color: #686868;
        }
        .cur-select {
          border: 1px solid #f23030;
          background-color: #fff6f6;
          color: #f23030;
        }
      }
    }
    .your-comments {
      padding: 0 0.3rem;
      #y-c-content {
        //display: block;
        box-sizing: border-box;
        padding: 0.2rem;
        width: 100%;
        height: 1rem;
        border: 1px solid #EAEAEA;
        background: #f0f2f5;
        border-radius: 10px;
        font-size: 0.32rem;
      }
      margin-bottom: 0.3rem;
    }
    .com-submit {
      display: block;
      height: 0.98rem;
      line-height: 0.98rem;
      font-size: 0.36rem;
      color: #fff;
      background-color: #f23030;
    }
    .sub-disabled {
      color: #333;
      background-color: #bbbaba;
    }
  }

</style>



