<template>
  <div id="send-present" class="swiper-container">
    <swiper :options="swiperOption" ref="mySwiper">
      <!-- slides -->
      <swiper-slide v-for="item in sendItem">
        <div class="item pre-i-wrapper">
          <div class="pre-pic">
            <img :src="item.pic">
          </div>
          <div class="pre-name">{{item.preName}}</div>
          <div class="pre-number">{{item.preNum}}京豆</div>
          <div class="pre-btn">赠送</div>
        </div>
      </swiper-slide>

      <!-- Optional controls -->
      <!--<div class="swiper-pagination"  slot="pagination"></div>-->
      <!--<div class="swiper-button-prev" slot="button-prev"></div>-->
      <!--<div class="swiper-button-next" slot="button-next"></div>-->
      <!--<div class="swiper-scrollbar"   slot="scrollbar"></div>-->
    </swiper>
  </div>

</template>

<script>
  // swiper options example:
  export default {
    name: 'swiper-present',
    data() {
      return {
        swiperOption: {
          slidesPerView: 4.2,
          freeMode: true,
          spaceBetween : 15
        },
        sendItem: [
          {
              pic: '../../static/imgs/flower.png',
              preName: '鲜花',
              preNum: 10
          },
          {
            pic: '../../static/imgs/jay.jpg',
            preName: '爱心',
            preNum: 20
          },
          {
            pic: '../../static/imgs/jay.jpg',
            preName: '饼干',
            preNum: 30
          },
          {
            pic: '../../static/imgs/jay.jpg',
            preName: '手套',
            preNum: 40
          },
          {
            pic: '../../static/imgs/jay.jpg',
            preName: '矿泉水',
            preNum: 50
          }

        ]
      }
    },
    // you can find current swiper instance object like this, while the notNextTick property value must be true
    // 如果你需要得到当前的swiper对象来做一些事情，你可以像下面这样定义一个方法属性来获取当前的swiper对象，同时notNextTick必须为true
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      // you can use current swiper instance object to do something(swiper methods)
      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
      console.log('this is current swiper instance object', this.swiper)
      this.swiper.slideTo(3, 1000, false)
    }
  }
</script>

<style lang="scss">
  @import '../assets/common.scss';
  #send-present {
    margin: 0.2rem;
    .item {
      width: 1.6rem;
      text-align: center;
      .pre-pic {
        display: table-cell;
        vertical-align: middle;
        width: 1.6rem;
        height:1.6rem;
        //background-color: #f7f7f7;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .pre-name {
        width: 98%;
        @include textEllipsis();
        margin-top: 0.16rem;
        font-size: 0.3rem;
        color: #686868;
      }
      .pre-number {
        width: 98%;
        @include textEllipsis();
        margin-top: 0.16rem;
        font-size: 0.26rem;
        color: #ff7d7d;
      }
      .pre-btn {
        display: inline-block;
        margin-top: 0.16rem;
        margin-bottom: 0.08rem;
        border: 1px solid #f23030;
        border-radius: 15px;
        background: #fff6f6;
        padding: 0.1rem 0.25rem;
        font-size: 0.22rem;
        color: #f23030;
      }
    }
  }

</style>



