<template>
  <div class="">
    <div class="urge" :class="{'urge-disabled': disabledBtn}" id="urge-list" @click="showPop">{{disabledBtn?'已经催单':'我要催单'}}</div>

    <transition name="fade">
      <template v-if="isPopShow">
        <div class="urge-pop">已通知小哥，火速派单中..</div>
      </template>
    </transition>
  </div>
</template>

<script>
  export default {
      name: 'co-dialog',
      data () {
          return {
              disabledBtn: false,
              isPopShow: false
          }
      },
      methods: {
          showPop: function() {
              let me = this;
              if (!this.disabledBtn) {
                  this.urgeRequest(function(res) {
                      if (res) {
                        me.popUp();

                        me.disabledBtn = true;
                        setTimeout(function() {
                          me.disabledBtn = false;
                        }, 4000)
                      }
                  });
              }

          },
          popUp: function() {
            let me = this;
            this.isPopShow = true;
            setTimeout(function() {
              me.isPopShow = false;
            }, 1000);
          },
          urgeRequest: function(callback) {
              console.log('催单成功！');
              callback(true);
          }

      }
  }
</script>

<style lang="scss">
  // vue 渐变动画-配合<transition>标签和css

  .urge {
    position: absolute;
    display: block;
    $u-width: 2.06rem;
    $u-height: 0.62rem;
    bottom: -15%;
    left: 50%;
    margin: -$u-width / 2;
    width: $u-width;
    height: $u-height;
    text-align: center;
    line-height: $u-height;
    font-size: 0.3rem;
    border-radius: 15px;
    background-color: rgba(0,0,0,0.6);
    color: #f0f2f5;
  }
  .urge-disabled {
    background-color: rgba(0, 0, 0, 0.4);
  }
  .urge-pop {
    position: fixed;
    margin:0 auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    width: 70%;
    text-align: center;
    height: 1.5rem;
    line-height: 1.5rem;
    background: rgba(0,0,0,0.6);
    border-radius: 10px;
    font-size: 16px;
    color: #fff;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0;
  }
</style>



