<template>
  <div class="send-info">
    <div class="up clearfix">
      <div class="earth"><span class="number">{{circle}}</span></div>
      <div class="foot-print">
        <div class="has-send">已配送</div>
        <div class="km"><span class="number">{{sendInfoData.distance}}</span>公里</div>
        <div class="circle">总里程足以绕地球<span class="number">{{circle}}</span>圈</div>
        <span>{{bookData.books?bookData.books[0].title:'初始化title'}}</span>
        <img :src="bookData.books?bookData.books[0].image : '' ">
      </div>
    </div>

    <!--todo 4.5 小件-->
    <template v-if="sendInfoData.isSmallList">
      <div class="down clearfix">
        <div class="item">
          <div class="title">配送总时长</div>
          <div class="content"><span class="number">{{sendInfoData.cost}}</span>天</div>
        </div>
        <div class="item">
          <div class="title">配送总单量</div>
          <div class="content"><span class="number">{{sendInfoData.list}}</span>单</div>
        </div>
        <div class="item">
          <div class="title">为我配送</div>
          <div class="content"><span class="number">{{sendInfoData.sendTimes}}</span>次</div>
        </div>
      </div>
    </template>

    <template v-else>
      <!--todo 4.5 大件-->
      <div class="down clearfix">
        <div class="item big-item">
          <div class="title">配送总时长</div>
          <div class="content"><span class="number">{{sendInfoData.cost}}</span>天</div>
        </div>
        <div class="item big-item">
          <div class="title">配送总单量</div>
          <div class="content"><span class="number">{{sendInfoData.list}}</span>单</div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  //import jsonp from 'jsonp'; // 或者在组件局部使用jsonp模块
  export default {
      name: 'co-send-info',
      created: function() {
          let me = this;
          this.$http.get('../../static/test/send-info.json').then(function(res) {
              console.log(res);
              me.sendInfoData = res.data;
          }).catch(function (err)  {
              console.log('Get data failed!')
          });

        this.$jsonp('https://api.douban.com/v2/book/search?q=php&count=100&start=0', null, function (err, data) {
          if (err) {
            console.error(err.message);
          } else {
            console.log(data);
            me.bookData = data;
          }
        });
      },
      data () {
          return {
              sendInfoData: {
                "isSmallList": true,
                "distance": 0,
                "cost": 0,
                "list": 0,
                "sendTimes": 0
              },
              bookData:[],
              image: 'https://img3.doubanio.com/mpic/s28377120.jpg'
          }
      },
      computed: {
          circle: function() {

              return Math.round(this.sendInfoData.distance / (Math.PI * 2 * 6400) * 100) / 100;
          }
      }


  }
</script>

<style lang="scss">
  @import "../assets/common.scss";
  .send-info {
    border-bottom: 0.2rem solid #f0f2f5;
    padding: 0.3rem 0.5rem;
    .up {
      .earth {
        position: relative;
        float: left;
        width: 2.1rem;
        height: 1.7rem;
        @include bgImg('../assets/imgs/earth.png');
        .number {
          position: absolute;
          font-size: 10px;
          top: 14%;  // 0.2 0.5
          right: 25%;
          color: #f198a0;
        }
      }
      .foot-print {
        float: right;
        .has-send {
          font-size: 0.38rem;
          color: #161418;
        }
        .km {
          margin-top:0.26rem;
          font-size: 0.28rem;
          color: #848689;
          .number {
            margin-right: 0.1rem;
            font-size: 0.46rem;
            color: #f23030;
          }
        }
        .circle {
          margin-top:0.26rem;
          font-size: 0.28rem;
          color: #81838e;
          .number {
            color: #f23030;
          }
        }

      }
    }
    .down {
      margin-top: 0.38rem;
      text-align: center;
      .item {
        float: left;
        width: 33.3%;
        .title {
          font-size: 0.28rem;
          color: #81838e;
        }
        .content {
          margin-top: 0.28rem;
          font-size: 0.22rem;
          color: #81838e;
          .number {
            font-size: 0.34rem;
            color: #161418;
          }
        }
      }
      .big-item {
        width: 50%;
      }
    }
  }
</style>



