<template>
  <div class="map">
    <h3 class="m-title">实时配送地图</h3>
    <div class="map-pic">
      <div class="ten-map" id="ten-map"></div>
      <div class="person">
        <i class="run"></i>
        <i class="distance">距您<span class="number">10.3</span>公里，小哥正在奔跑中......</i>
      </div>
      <div class="destination"></div>
    </div>
    <!-- todo4.5 -->
  </div>
</template>

<script>
    export default {
        name: 'co-map',
        data () {
          return {
            test: ''
          }
        },
        methods: {

        },
        mounted: function () {
            let center = new qq.maps.LatLng(39.982163, 116.306070);
            let map = new qq.maps.Map(document.getElementById("ten-map"), {
              center: center,
              zoom: 16
            });
            //设置Marker自定义图标的属性，size是图标尺寸，该尺寸为显示图标的实际尺寸，origin是切图坐标，该坐标是相对于图片左上角默认为（0,0）的相对像素坐标，anchor是锚点坐标，描述经纬度点对应图标中的位置
            let anchor = new qq.maps.Point(0, 39),
              size = new qq.maps.Size(64, 72),
              origin = new qq.maps.Point(0, 0),
              scaleSize = new qq.maps.Size(26.5, 30), //图片缩放后的尺寸
              icon = new qq.maps.MarkerImage(
                ".../assets/imgs/destination.png", //图片url
                size,
                origin,
                anchor,
                scaleSize
              );

            let marker = new qq.maps.Marker({
              map: map,
              position: center
            });
            marker.setIcon(icon);
        }
    }
</script>


<style lang="scss">
  @import "../assets/common.scss";
  .map {
    position: relative;
    padding: 0.3rem;
    padding-bottom: 0;
    border-bottom: 0.2rem solid #f0f2f5;
    .m-title {
      font-size: 0.3rem;
      margin-bottom: 0.3rem;
    }
    .map-pic {
      position: relative;
      width: 6.9rem;
      height: 5.18rem;
      .ten-map {
        width: 100%;
        height: 100%;
      }
      .person {
        position: absolute;
        top: 0.3rem;
        left: 0.3rem;
        .run {
          position: absolute;
          top: 0;
          left: 0;
          z-index: 1;
          display: block;
          width: 0.6rem;
          height: 0.7rem;
          @include bgImg('../assets/imgs/people.png');
        }
        .distance {
          position: absolute;
          top: 0;
          left: 0.45rem;
          text-align: center;
          display: block;
          width: 4.2rem;
          height: 0.62rem;
          line-height: 2;
          font-size: 0.24rem;
          color: #fff;
          @include bgImg('../assets/imgs/pop.png');

        }
      }
      .destination {
        position: absolute;
        top: 2rem;
        left: 4rem;
        width: 0.54rem;
        height: 0.62rem;
        @include bgImg('../assets/imgs/destination.png');
      }
    }

  }
</style>



