<template>
  <div class="evaluate" id="evaluate" @click="showPop">
    <div class="eva-content">评价配送员</div>
  </div>
</template>

<script>
    import bus from '../assets/eventBus';
    export default {
        name: 'co-evaluate-btn',
        data () {
            return {

            }

        },
        methods: {
            showPop () {
                bus.$emit('showEvaPop');
            }
        }
    }
</script>

<style lang="scss">
  @import "../assets/common.scss";
  .evaluate {
    position: fixed;
    bottom: 0;
    z-index: 20;
    height: 0.96rem;
    width: 100%;
    max-width: 750px;
    background-color: #f23030;
    text-align: center;
    color: #fff;
    font-size: 0.36rem;
    line-height: 0.96rem;
    .eva-content {
      $eva-width: 0.4rem;
      position: relative;
      display: inline-block;
      margin-left: $eva-width + 0.16rem;
      &:before {
        position: absolute;
        top: 50%;
        left: -$eva-width - 0.16rem;
        content: '';
        width: 0.4rem;
        height: 0.4rem;
        margin-top: -0.2rem;
        display: inline-block;
        float: right;
        @include bgImg('../../static/imgs/write.png')
      }
    }
  }
</style>



